import React from "react";
import "./timeline-item.css";

const TimelineItem = ({ timeStart, timeStop, exist }) => {

  let hourLength = 64;
  let minLength = hourLength / 60; 

  let diff = timeStop - timeStart;
  let getWidth = (sec) => {
    let px = (minLength / 60) * sec;
    return px;
  };
  let width = getWidth(diff);
  const styles = {
    isEmpty: {
      backgroundColor: "#fff",
       width: `${width}px` ,
    },
    default: {
      backgroundColor: "#f8e5f7",
      width: `${width}px` ,
    },
    
  };

  return (
    <div className="time-min"
    style={exist ? styles.isEmpty : styles.default} 
    >
    </div>
  );
};
export default TimelineItem;
