import React from "react";
import "./empty-timeline.css";

const EmptyTimeline = ({item,  timeStart, timeStop}) => {

    let hourLength = 64; // px за 1 час 
    let minLength = hourLength / 60; 
  
    let diff = timeStop - timeStart;
    let getWidth = (sec) => {
      let px = (minLength / 60) * sec;
      return px;
    };
    let width = getWidth(diff);

    const styles = {
        isEmpty: {
          backgroundColor: "#fff",
           width: `${width}px` ,
        },
        default: {
          width: `${width}px` ,
        },
        
      };

    return <div
    style={styles.default}
    className="EmptyTimeline">
    
    </div>
}
export default EmptyTimeline