import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import "./timeline.css";
import TimelineItem from "../timeline-item";
import EmptyTimeline from "../empty-timeline";
import { v4 as uuidv4 } from "uuid";

//  timelineList["12-02-2020"].
const Timeline = ({ day, worklogList }) => {
  let timeArr = worklogList[day];

  const insert = (arr, index, newItem) => [
    ...arr.slice(0, index),
    newItem,
    ...arr.slice(index),
  ];

  // заполняем пустые места в тайм линии
  let getEmptyPlace = (arr) => {
    let newArr = [];
    for (let i = 0; i < arr.length; i++) {
      if ([i] < arr.length) {

        if ([i] < 1 && arr[i].timeStart === "28800") { // если начали с 8 утра 
          newArr.push(arr[i]);
        }
        if ([i] < 1 && arr[i].timeStart !== "28800") { // если начали не с 8 утра

          let el = {
            timeStart: "28800",
            timeStop: arr[i].timeStart,
            status: "пусто",
          };
          newArr.push(el);
          newArr.push(arr[i]);
        }
        if ([i] > 0 && arr[i].timeStart === arr[i - 1].timeStop) { // если совпадает начало и конец ворклога
          newArr.push(arr[i]);
        }
        if ([i] > 0 && arr[i].timeStart !== arr[i - 1].timeStop) { // если не совпадает начало и конец ворклога,рисуем пустоту

          let el = {
            timeStart: arr[i - 1].timeStop,
            timeStop: arr[i].timeStart,
            status: "пусто",
          };
          newArr.push(el);
          newArr.push(arr[i]);
        }
      }
    }
    newArr.sort((prev, next) => {
      if (prev.timeStart < next.timeStart) return -1;
      if (prev.timeStart > next.timeStart) return 1;
    });
    return newArr;
  };

  timeArr = getEmptyPlace(timeArr);
  return (
    <div className="t-wrap">
      <div className="timeline-w">
        {timeArr.map((el) => {
          if (el.status === "пусто") {
            return (
              <EmptyTimeline
                item={el}
                timeStart={el.timeStart}
                timeStop={el.timeStop}
                key={uuidv4()}
              />
            );
          } else {
            return (
              <TimelineItem
                id={el.id}
                key={uuidv4()}
                timeStart={el.timeStart}
                timeStop={el.timeStop}
                item={el}
              />
            );
          }
        })}
      </div>

      <div className="times">
        <div className="times-el">07:00</div>
        <div className="times-el">08:00</div>
        <div className="times-el">09:00</div>
        <div className="times-el">10:00</div>
        <div className="times-el">11:00</div>
        <div className="times-el">12:00</div>
        <div className="times-el">13:00</div>
        <div className="times-el">14:00</div>
        <div className="times-el">15:00</div>
        <div className="times-el">16:00</div>
        <div className="times-el">17:00</div>
        <div className="times-el">18:00</div>
        <div className="times-el">19:00</div>
        <div className="times-el">20:00</div>
      </div>
    </div>
  );
};

const mapStateToProps = ({ timelineList }) => {
  return { timelineList };
};
//   const mapDispatchToProps = (dispatch) => {
//     return {
//       onAddedToCart: (id) => dispatch(worklogAddedToCart(id)),
//     };
//   };

export default connect(mapStateToProps, null)(Timeline);
